body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background-color: rgb(21, 21, 21);
}

.pointer {
  cursor: pointer;
}

/* CONTAINER */
.container {
  margin: 10px auto;
  padding: 10px;
  border-radius: 5px;

  width: calc(100% - 20px);
  max-width: 500px;
}
@media screen and (max-width: 500px){
  .container {
    border-radius: 0px;
  }
}

.container-white {
  background-color: #fefefe;
}

/* COLOR FONTS */
.text-white {
  color: #ede31c;
}

.text-center {
  text-align: center;
}

.div-center {
  width: 300px;
  margin: 20px auto;
}
.div-center > span {
  color: #ababab;
  font-size: 0.8rem;
}
.div-center > span > a {
  text-decoration: none;
  color: #dedede;
  border-bottom: 1px solid #dedede;
}

/* APP */
.App > .head {
  background-color: rgb(2, 85, 94);
}

/* LIVRO */
.livro {
  display: -ms-flexbox;
  display: flex;
  padding: 20px 10px;
}

.livro-nome, .livro-data{
  -ms-flex:1 1;
      flex:1 1;
}

.livro-nome, .livro-vote {
  font-size: 1.3rem;
  color: #222;
}

.livro-up {
  color: rgb(92, 186, 92);
}
.livro-down {
  color: rgb(237, 80, 80);
}
.livro-up-select {
  color: green;
}
.livro-down-select {
  color: red;
}

.livro-data {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.livro-data > span {
  padding: 0 10px;
}

/* TITULO */
.titulo {
  font-size: 3rem;
  margin: 0;
}

/* INPUT */
.NovoLivro {
  padding: 15px 10px;
  font-size: 1.1rem;
}

.NovoLivro > form > input, .NovoLivro > form > button {
  margin: 10px 10px 0 0;
  padding: 7px 10px;
  border-radius: 5px;
  border: 1px solid #999;
  font-size: 1rem;
  font-weight: 100;
}

.NovoLivro > form > button {
  background-color: rgb(25, 106, 97);
  border: 0px;
  color: #fefefe;
  cursor: pointer;
  -webkit-transition: 0.3s all;
  -o-transition: 0.3s all;
  transition: 0.3s all;
}

.NovoLivro > form > button:hover {
  background-color: rgb(23, 93, 129);
}


